:root {
  --violet: hsl(273, 75%, 66%);
  --shadowViolet: hsla(261, 75%, 27%, 0.5);
  --red: hsl(14, 88%, 65%);
  --softBlue: hsl(240, 73%, 65%);
  --darkBlue: hsl(238, 29%, 16%);
  --darkGrayshBlue: hsl(237, 12%, 33%);
  --lightGrayshBlue: hsl(240, 5%, 91%);
  --grayshBlue: hsl(240, 6%, 50%);
}

:root {
  --font-kumbh: "Kumbh Sans", sans-serif;
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, var(--violet), var(--softBlue));
  display: grid;
  align-content: center;
  padding-top: 9rem;
}

li {
  list-style: none;
}

.wrapper {
  background-color: #fff;
  max-width: 32.7rem;
  width: 100%;
  margin: 0 auto;
  padding: 13.2rem 0 4.8rem;
  position: relative;
  border-radius: 23px;
  box-shadow: 0 5rem 5rem -2rem var(--shadowViolet);
  display: grid;
}
@media (min-width: 48em) {
  .wrapper {
    grid-template-columns: repeat(2, 1fr);
    max-width: 70rem;
    padding: 6.5rem 0 5rem;
    overflow: hidden;
  }
}
@media (min-width: 62em) {
  .wrapper {
    grid-template-columns: repeat(2, 1fr);
    max-width: 96rem;
    padding: 6.5rem 0 8.5rem;
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
figure,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 62.5%;
}

body {
  font-family: var(--font-kumbh);
}

.heading-primary {
  font-size: 3.2rem;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 48em) {
  .heading-primary {
    text-align: left;
  }
}

.img-wrapper {
  max-width: 72.4%;
  width: 100%;
  margin: 0 auto;
  position: absolute;
  top: -11rem;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 48em) {
  .img-wrapper {
    position: static;
    grid-column: 1/2;
    align-self: center;
    transform: translateX(-3rem);
    max-width: 100%;
  }
}

.img-content {
  position: relative;
}
.img-content::after {
  content: "";
  background-image: url(../images/bg-pattern-mobile.svg);
  position: absolute;
  top: 10rem;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  z-index: -1;
}
@media (min-width: 48em) {
  .img-content::after {
    background-image: url(../images/bg-pattern-desktop.svg);
    background-size: contain;
    transform: scale(1.4);
    top: 0;
    left: 4.5rem;
  }
}
@media (min-width: 75em) {
  .img-content::after {
    transform: scale(2.4);
    top: -5rem;
    left: -8rem;
  }
}
.img-content .img-box {
  display: none;
}
@media (min-width: 48em) {
  .img-content .img-box {
    display: block;
    position: absolute;
    top: 10rem;
    width: 15rem;
  }
}
@media (min-width: 62em) {
  .img-content .img-box {
    display: block;
    position: absolute;
    top: 15rem;
    width: 18rem;
  }
}

.faq {
  margin: 0 2.4rem;
  z-index: 10;
}
@media (min-width: 48em) {
  .faq {
    grid-column: 2/-1;
    margin-right: 4rem;
  }
}
@media (min-width: 62em) {
  .faq {
    margin-right: 6rem;
  }
}
@media (min-width: 75em) {
  .faq {
    margin-right: 9rem;
  }
}
.faq__list {
  margin-top: 3.8rem;
}
.faq__item {
  font-size: 1.3rem;
  color: var(--darkGrayshBlue);
  display: grid;
  grid-template-columns: 1fr 0.8rem;
  column-gap: 3.5rem;
  row-gap: 1.2rem;
  align-items: center;
  padding-bottom: 2rem;
  border-bottom: 1px solid var(--lightGrayshBlue);
  cursor: pointer;
}
.faq__item:hover {
  color: var(--red);
}
@media (min-width: 62em) {
  .faq__item {
    font-size: 1.4rem;
  }
}
.faq__item:not(:first-child) {
  padding-top: 2rem;
}
.faq__description {
  display: none;
  font-size: 1.2rem;
  grid-column: 1/-1;
}